import React, { useState, useEffect } from 'react';
import { Modal, Button, Form, Input } from 'antd';
import { fromLonLat } from 'ol/proj';
import { Vector as VectorLayer } from 'ol/layer';
import { Vector as VectorSource } from 'ol/source';
import { Icon, Style } from 'ol/style';
import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
import './MarkerForm.css';
import MapView from './Map';
const MarkerForm = ({ onMarkerSave }) => {
  const [visible, setVisible] = useState(false);
  const [markers, setMarkers] = useState([]);
  const [vectorLayer, setVectorLayer] = useState(null);

  useEffect(() => {
    const storedMarkers = localStorage.getItem('markers');
    if (storedMarkers) {
      setMarkers(JSON.parse(storedMarkers));
    }

    const initialVectorLayer = new VectorLayer({
      source: new VectorSource(),
    });

    setVectorLayer(initialVectorLayer);
  }, []);

  const handleOpenModal = () => {
    setVisible(true);
  };

  const handleCloseModal = () => {
    setVisible(false);
  };

  const handleSubmit = (values) => {
    const newMarker = {
      title: values.title,
      description: values.description,
      coordinates: [values.Lon, values.Lat], // Замените координаты на нужные вам значения
    };

    const newMarkerFeature = new Feature({
      geometry: new Point(fromLonLat(newMarker.coordinates)),
      title: newMarker.title,
      description: newMarker.description,
    });

    vectorLayer.getSource().addFeature(newMarkerFeature);
    
    const updatedMarkers = [...markers, newMarker];
    setMarkers(updatedMarkers);
    localStorage.setItem('markers', JSON.stringify(updatedMarkers));
    
    handleCloseModal();
  };

  useEffect(() => {
    const updateMapHandler = () => {
      const storedMarkers = localStorage.getItem('markers');
      if (storedMarkers) {
        const markers = JSON.parse(storedMarkers);
        onMarkerSave(markers);
      }
    };

    window.addEventListener('storage', updateMapHandler);

    return () => {
      window.removeEventListener('storage', updateMapHandler);
    };
  }, [onMarkerSave]);

  return (
    <div className="marker-form">
      <Button type="primary" onClick={handleOpenModal}>
        Добавить маркер
      </Button>
      <Modal
        visible={visible}
        title="Добавление маркера"
        onCancel={handleCloseModal}
        footer={null}
      >
        <Form onFinish={handleSubmit}>
          <Form.Item
            name="title"
            label="Название"
            rules={[{ required: true, message: 'Пожалуйста, введите название маркера' }]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="description"
            label="Описание"
          >
            <Input.TextArea />
          </Form.Item>
          <Form.Item
            name="Lon"
            label="Долгота"
            rules={[{ required: true, message: 'Пожалуйста, введите долготу' }]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="Lat"
            label="Ширина"
            rules={[{ required: true, message: 'Пожалуйста, введите Ширину' }]}
          >
            <Input />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              Сохранить
            </Button>
          </Form.Item>
        </Form>
      </Modal>
      {markers.map((marker, index) => (
        <div key={index}>{marker.title} - {marker.description}</div>
      ))}
      <MapView markers={markers} />
    </div>
  );
};

export default MarkerForm;
