import React, { useEffect } from 'react';
import { Map, View } from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import { fromLonLat } from 'ol/proj';
import { Vector as VectorLayer } from 'ol/layer';
import { Vector as VectorSource } from 'ol/source';
import { Icon, Style } from 'ol/style';
import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
import Overlay from 'ol/Overlay';
import Popup from 'ol-popup';
import './MarkerForm.css';

const MapView = ({ markers }) => {
  useEffect(() => {
    const map = new Map({
      target: 'map',
      layers: [
        new TileLayer({
          source: new OSM(),
        }),
      ],
      view: new View({
        center: fromLonLat([39.7015, 47.2357]), // Ростов-на-Дону
        zoom: 12,
      }),
    });

    const vectorSource = new VectorSource();
    const vectorLayer = new VectorLayer({
      source: vectorSource,
    });
    map.addLayer(vectorLayer);

    const overlay = new Overlay({
      element: document.getElementById('popup'),
      positioning: 'bottom-center',
      stopEvent: false,
      offset: [0, -10],
    });
    map.addOverlay(overlay);

    const popup = new Popup();
    overlay.setElement(popup.getElement());

    const updateMapHandler = () => {
      vectorSource.clear();
      const storedMarkers = localStorage.getItem('markers');
      if (storedMarkers) {
        const markers = JSON.parse(storedMarkers);
        markers.forEach((marker) => {
          const { title, description, coordinates } = marker;
          const markerFeature = new Feature({
            geometry: new Point(fromLonLat(coordinates)),
            title: title,
            description: description,
          });
          vectorSource.addFeature(markerFeature);

          // При наведении на маркер отобразить всплывающее окно с информацией о нем
          markerFeature.on('mouseover', function (event) {
            console.log("Навёлся")
            const coordinate = event.coordinate;
            const content = `<h3>${title}</h3><p>${description}</p>`;
            popup.show(coordinate, content);
          });

          // При уходе курсора с маркера скрыть всплывающее окно
          markerFeature.on('mouseout', function () {
            popup.hide();
          });
        });
      }
    };

    const mapElement = document.getElementById('map');
    if (mapElement) {
      mapElement.addEventListener('updateMap', updateMapHandler);
    }

    // Вызываем обновление карты при монтировании компонента
    updateMapHandler();

    return () => {
      if (mapElement) {
        mapElement.removeEventListener('updateMap', updateMapHandler);
      }
    };
  }, []);

  useEffect(() => {
    const mapElement = document.getElementById('map');
    if (mapElement) {
      mapElement.dispatchEvent(new Event('updateMap')); // Создание и вызов события 'updateMap'
    }
  }, [markers]);

  return (
    <div>
      <div id="map" style={{ width: '100%', height: '100vh' }}></div>
      <div id="popup" className="ol-popup"></div>
    </div>
  );
};

export default MapView;



